    "status": "2 BLOCKS CEASED & 2 CANCELED",
    "description": "Construction of the NPP started in 1981 after a decade of planning. The chosen location was dictated foremostly by the need of providing electricity to the grid in the northern part of Poland. Until this point, energy was imported from the southern region of Silesia, but the losses during the energy transmission were as high as 10% due to the 500 km long distance. The initial investment envisioned two reactor blocks with turbine halls, a training facility, and a hydro plant to support the backup systems for NPP arnowiec. The construction site took longer than anticipated, and in 1989, the site was conserved in order to review the project while Poland was transitioning to democracy.  \\nEJ arnowiec became a UNPP in 1990, after losing financing and dealing with a public relations crisis. The official reason for abandoning the construction was the lack of need for such a facility. We have to keep in mind that the oil price had dropped by more than 70% since the inception of the project. Nonetheless, the ceased construction generated contractual penalties as high as the budget required to finish the facility. The equipment (including reactors) that had already been delivered to the site was sold to NPPs abroad that were under construction at the time. In modern days, the original location of UNPP Zarnowiec is still considered optimal for another, indeed the first, NPP in the country. ",
