    "set_planned_pop": "~20,000",
    "set_cur_pop": "~5,500",
    "set_description": "Located less than 5 km from planned confinement buildings, Nuclear City was established in 1980, 3 years before the start of the construction of the NPP. Initially designed with 4200 dwellings in mind, it was equipped with schools, clinics, and all the marvels of a socialist top-down planned city. It was proudly compared to the city of Pripyat, which at the time had only 6 more years of life. This socialist heterotopia in the making was providing housing for construction workers, scientists, and engineers, including Soviet consultants from Atomenergoexport. In the meantime, new Cuban nuclear personnel was getting trained in the USSR, planning to settle here once they complete their education.\\nCurrently, the settlement is half-empty. In the remote location, the half-finished city connected by half-finished roads has only been thinning since 1989. Employment can be found 30 km away in distant Cienfuegos, where a port and industrial complex is located. Once considered a perfect location away from the capital, it has become a periphery chained to an unfinished concrete cathedral of science. The pursuit of a new complete future for the region rendered 1300 Cuban nuclear scientists unable to find employment; they thus dispersed. The recent collaboration with Venezuela revived the regions oil refineries, making the pursuit for scientific and technological development of the region redundant. ",
