    "set_description": "The community tied to the Belene UNPP is the ancient town of Belene (Dimum). The project did not create a new satellite city but reshaped an old one. Starting in the 1980s, construction brought an influx of workers from across the CMEA bloc, including the USSR and Hungary, nearly doubling the town's population. This created a boomtown atmosphere, with investment pouring into a new residential district and a sense of a modern, industrial future replacing the quiet, agricultural past. Belene's identity became inextricably linked to the promise of the nuclear energy facility being built 7 km away.\\nThe cancellations in 1990 and 2012 subjected Belene to devastating boom-and-bust cycles. Each halt triggered a mass exodus of workers, a population collapse, and a spike in unemployment. Public opinion became deeply polarized between those who saw the plant as the only path to prosperity and those who feared its geopolitical and environmental risks. This perpetual frustration has held the community in a state of suspended animation for decades, unable to move forward while the spectre of the UNPPwith its vast, empty site and two unused reactorslooms over its future.",
